.movie-list {
    padding-top: 3%;
}

.movie-list-header {
    padding-left: 12%;
}

.movie-list-content {
    padding-top: 3%;
    display: grid;
    grid-gap: 20px 10px;
    grid-template-columns: auto auto auto auto auto;
    margin: auto;
    max-width: 1500px;
    max-height: fit-content;
}